@import '../../scss/styles';

$block: app-wrapper;
$blockTablet: modifier($block, tablet);

.#{$block} {
  position: relative;
  display: flex;
  background-color: $color-main-background;

  &--tablet {
    flex-direction: column;
  }
}

:export {
  block: $block;
  blockTablet: $blockTablet;
}
